<template>
  <div id="puniuHeader">
    <div class="puniu-logo">
      <img src="../assets/imgs/logo-bai.png" alt />
    </div>
    <div class="puniu-nav">
      <el-menu
        :default-active="activeIndex"
        class="el-menu-demo"
        mode="horizontal"
        @select="handleSelect"
        active-text-color="#2F68B2"
      >
        <el-menu-item index="1">关于我们</el-menu-item>
        <el-submenu index="2">
          <template slot="title">产品介绍</template>
          <router-link to="/hifiDas">
            <el-menu-item index="2-1">HiFi - DAS</el-menu-item>
          </router-link>
        </el-submenu>
        <el-submenu index="3">
