        </el-menu-item>
      </el-menu>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data() {
    return {
      activeIndex: "1",
    };
  },
  components: {},
  mounted() {},
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    }
  },
  porps: ['activeNumber'],
  created() {
      console.log(this.activeNumber)
    this.activeIndex = this.activeNumber;
    console.log(this.activeIndex);
  }
};
</script>

<style>
#puniuHeader {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
}
</style>



