import Vue from 'vue'
import Router from 'vue-router'
import PuniuHome from '@/components/puniuHome'
import HelloWorld from '@/components/HelloWorld'
import HifiDas from '@/components/hifiDas'
import AboutUs from '@/components/aboutUs'
import PuniuApplication from '@/components/puniuApplication'
import PuniuDynamic from '@/components/puniuDynamic'
import YouqiWarning from '@/components/youqiWarning'
import YouqiProduction from '@/components/youqiProduction'
import XianxingWarning from '@/components/xianxingWarning'
import Recruitment from '@/components/recruitment'





Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'PuniuHome',
      component: PuniuHome
    }, {
      path: '/HelloWorld',
      name: 'HelloWorld',
      component: HelloWorld
    }, {
      path: '/hifiDas',
      name: 'HifiDas',
      component: HifiDas
    }, {
      path: '/aboutUs',
      name: 'AboutUs',
      component: AboutUs
    }, {
      path: '/puniuApplication',
      name: 'PuniuApplication',
      component: PuniuApplication
    }, {
      path: '/puniuDynamic',
      name: 'PuniuDynamic',
      component: PuniuDynamic
    }, {
      path: '/youqiWarning',
      name: 'YouqiWarning',
      component: YouqiWarning
    }, {
      path: '/youqiProduction',
      name: 'YouqiProduction',
      component: YouqiProduction
    }, {
      path: '/xianxingWarning',
      name: 'XianxingWarning',
      component: XianxingWarning
    }, {
      path: '/recruitment',
      name: 'Recruitment',
      component: Recruitment
    }
  ]
})



