<template>
  <div class="number-grow-warp">
    <span ref="numberGrow" :data-time="time" class="number-grow" :data-value="value">0</span>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  props: {
    time: {
      type: Number,
      default: 20
    },
    value: {
      type: Number,
      default: 500
    }
  },
  created() {
  },
  data() {
    return {};
  },
  components: {},
  mounted() {
    this.numberGrow(this.$refs.numberGrow);

  },
  watch: {
    value(val) {
      if (val > 0) {
        this.values = val;
        this.numberGrow(this.$refs.numberGrow);
      }
    }
  },
  methods: {
    numberGrow(ele) {
      let arr = this;
      let step = parseInt((arr.value * 10) / (arr.time * 1000));
      console.log(step);
      let current = 0;
      let start = 0;
      let t = setInterval(() => {
        start += step;
        console.log(t);
        if (start > arr.value) {
          clearInterval(t);
          start = arr.value;
          t = null;
        }
        if (start == 0) {
          start = arr.value;
          clearInterval(t);
        }
        if (current === start) {
          return;
        }
        current = start;
        ele.innerHTML = current
          .toString()
          .replace(/(\d)(?=(?:\d{3}[+]?)+$)/g, "$1,");
      }, 20);
    }
  }
};
</script>

<style>
.number-grow-warp {
  transform: translateZ(0);
}
.number-grow {
  display: block;
}
</style>



