 的系统指标")]),_vm._v(" "),_c('div',{staticClass:"das-text-list hello wow slideInUp",attrs:{"data-wow-offset":"50","data-wow-duration":"3s"}},[_c('h4',[_vm._v("1）传感距离 — 不小于 3 公里")]),_vm._v(" "),_c('h4',{staticStyle:{"padding-left":"180px"}},[_vm._v("在满足一定的应变灵敏度的要求下，最长的探测距离;")])]),_vm._v(" "),_c('div',{staticClass:"das-text-list hello wow slideInUp",attrs:{"data-wow-offset":"50","data-wow-duration":"3s"}},[_c('h4',[_vm._v("2）响应带宽 — 3 公里长传感光纤条件下，不小于 10 千赫兹")]),_vm._v(" "),_c('h4',{staticStyle:{"padding-left":"180px"}},[_vm._v("可以被探测的应变信号的最大频率；")])]),_vm._v(" "),_c('div',{staticClass:"das-text-list hello wow slideInUp",attrs:{"data-wow-offset":"50","data-wow-duration":"3s"}},[_c('h4',[_vm._v("3）空间分辨率 — 1米")]),_vm._v(" "),_c('h4',{staticStyle:{"padding-left":"180px"}},[_vm._v("两个同时发生的振动事件可以被区分的最小间距;;")])]),_vm._v(" "),_c('div',{staticClass:"das-text-list hello wow slideInUp",attrs:{"data-wow-offset":"50","data-wow-duration":"3s"}},[_c('h4',[_vm._v("4）应变灵敏度 — 整段传感光纤上都优于 1 纳应变")]),_vm._v(" "),_c('h4',{staticStyle:{"padding-left":"180px"}},[_vm._v("可以被探测到的应变信号的最小峰-峰值。")])]),_vm._v(" "),_c('div',{staticClass:"das-xian"}),_vm._v(" "),_c('h2',{staticClass:"hello wow slideInUp",staticStyle:{"margin-top":"50px","margin-bottom":"20px"},attrs:{"data-wow-duration":"3s"}},[_vm._v("HiFi-DAS 的四项指标的全面提升")]),_vm._v(" "),_c('div',{staticClass:"das-text-list hello wow slideInUp",attrs:{"data-wow-duration":"3s"}},[_c('h4',[_vm._v("1）基于Φ-OTDR 的 DAS 系统中的问题:")])]),_vm._v(" "),_c('p',{staticClass:"hello wow slideInUp",attrs:{"data-wow-offset":"50","data-wow-duration":"3s"}},[_vm._v("\n        Φ-OTDR 的空间分辨率由探测光脉冲的脉宽决定，脉宽越短则空间分辨率越高，反之亦然。但是在探测光脉冲的峰值功率受限的情况下，脉宽越短则平均功率越小，这又会影响其他三个指标，具体原因如下。\n        "),_c('br'),_vm._v(" "),_c('br'),_vm._v("Φ-OTDR 是通过解调瑞利背向散射信号(Rayleigh Backscattering，RBS)的相位来获取外界的振动信号的，因此相位项的信噪比直接决定了应变灵敏度和传 感距离。而相位项是通过 I/Q 解调算法从 RBS 的强度项计算得到，因此相位项 的信噪比直接由强度项信噪比决定。RBS 强度项信噪比又是由探测光脉冲的平均功率决定。因此在Φ-OTDR 中，提高空间分辨率的同时，就一定会降低应变灵敏度和传感距离。\n        "),_c('br'),_vm._v(" "),_c('br'),_vm._v("另一方面，根据奈奎斯特采样定理，响应带宽由探测光脉冲的有效发射频率决定。因为Φ-OTDR 的 RBS 强度项信噪比很差，需要通过多次发射探测光脉冲再求平均的方法来提高信噪比，这样就会成倍地降低有效发射频率，进而降低系统的响应带宽。所以Φ-OTDR 的空间分辨率和响应带宽也是矛盾的。\n        想要解决上述问题，必须要用新的系统结构和解调算法。\n      ")]),_vm._v(" "),_c('div',{staticClass:"das-text-list hello wow slideInUp",attrs:{"data-wow-offset":"50","data-wow-duration":"3s"}},[_c('h4',[_vm._v("2）基于 TGD-OFDR 的 HiFi-DAS 的改进:")])]),_vm._v(" "),_c('p',{staticClass:"hello wow slideInUp",attrs:{"data-wow-offset":"50","data-wow-duration":"3s"}},[_vm._v("TGD-OFDR 与Φ-OTDR 最大的区别在于，TGD-OFDR 的探测光脉冲是线性扫频的(Linear Frequency Modulated，LFM)，即光脉冲的光频率是随时间线性变化的。因为探测光脉冲的性质不同，所以在提取 RBS 相位项之前，我们需要先用匹配滤波算法对 RBS 信号进行脉冲压缩，将线性扫频的脉冲信号压缩成一个单频的脉冲信号，而这个脉冲信号的脉宽是由线性扫频信号的扫频范围决定的。所以 TGD-OFDR 的空间分辨率和探测光脉冲的脉宽不再有关。如果我们发射扫频范围大、脉宽长的探测光脉冲，那么我们就可以同时获得高的空间分辨率、高的应变灵敏度、长的传感距离、以及高的响应带宽，四项重要指标得到全面提升，从根本上解决了Φ-OTDR的问题。")]),_vm._v(" "),_c('h2',{staticClass:"hello wow slideInUp",staticStyle:{"margin-top":"50px","margin-bottom":"20px"},attrs:{"data-wow-offset":"50","data-wow-duration":"3s"}},[_vm._v("消除测量“死区”，提高系统的可靠性")]),_vm._v(" "),_c('p',{staticClass:"hello wow slideInUp",attrs:{"data-wow-offset":"50","data-wow-duration":"3s"}},[_vm._v("“死区”— 因为光的干涉效应和偏振效应，传感光纤上某些区域的 RBS 信号的强度信噪比会非常差，进而导致这些区域的应变灵敏度非常差，甚至失去了传感的能力，这些区域被称为“死区”。我们提出的脉冲频分法和相位旋转平均法， 再结合偏振分集接收器，在领域内第一次有效地解决了由光的干涉效应和偏振效应导致的传感“死区”。")]),_vm._v(" "),_c('div',{staticClass:"siqu-img hello wow slideInUp",attrs:{"data-wow-offset":"50","data-wow-duration":"3s"}},[_c('div',{staticClass:"siqu-img-list"},[_c('div',{staticClass:"siqu-img-top"},[_c('img',{attrs:{"src":require("../assets/imgs/siqu-left.png"),"alt":""}})]),_vm._v(" "),_c('div',{staticClass:"siqu-img-bottom"},[_vm._v("有“死区”情况")])]),_vm._v(" "),_c('div',{staticClass:"siqu-img-list"},[_c('div',{staticClass:"siqu-img-top"},[_c('img',{attrs:{"src":require("../assets/imgs/siqu-right.png"),"alt":""}})]),_vm._v(" "),_c('div',{staticClass:"siqu-img-bottom"},[_vm._v("无“死区”情况")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('footer'